import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import {
    Fab, Radio, Button, RadioGroup, FormControl,
    FormHelperText, Typography, FormControlLabel, Grid, TextField
} from "@material-ui/core";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import * as Yup from 'yup'
import { Autocomplete } from "@material-ui/lab"

import { Formik, Form, ErrorMessage } from 'formik'

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { MoonLoader } from 'react-spinners';

import Swal from 'sweetalert2'
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { columns, data, errorCols, errorOptions } from "./data_header_table";
import authService from "../../../utils/services/auth";


let departamentos = []
let usuario = null
let permissoesUsuario = []
let sexos = []
export default function Usuarios() {


    const getMuiTheme = () =>
        createMuiTheme({
            overrides: {
                MUIDataTableHeadCell: {
                    fixedHeader: { backgroundColor: "darkblue", color: "white" }
                },
                MUIDataTableSelectCell: {
                    headerCell: { backgroundColor: "darkblue" },
                    checkboxRoot: { color: "white", filter: "brightness(0.5)" },
                    expandDisabled: {
                        // Soft hide the button.
                        visibility: "hidden"
                    }
                }
            }
        });

    const [isFormValid, setIsFormValid] = useState(false);
    const [open, setOpen] = React.useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [updateDom, setUpdateDom] = useState(false);
    const [carregando, setCarregando] = useState(true);
    const [usuarios, setUsuarios] = useState([]);
    const [isCampoHabilitado, setIsCampoHabilitado] = useState(false);



    useEffect(() => {
        usuario = authService.getCurrentUser();
        setTimeout(() => {
            setCarregando(false);
        }, 5000);
        usuario.authorities[0] === 'ADMIN' ? init() : usuarioLagado();
        init();
    }, [])

    async function init() {
        setCarregando(true);
        const [usuarioRequest, dptRequest, permissoesRequest, requestSexo] = await Promise.all([
            requesicoes().getUsuarios('?campos=id,nome,apelido,permissoes,ativo,sexo'),
            requesicoes().buscarDepartamentos(), requesicoes().getPrmissoes(),
            requesicoes().buscarSexo()
        ]);
        sexos = requestSexo.data
console.log('usuario.authorities[0] ',usuario.authorities[0]);
        if (usuario.authorities[0] === 'RA_N1') {
           const usuariosRa = usuarioRequest.data;
            setUsuarios(usuariosRa);
        } else if (usuario.authorities[0] === 'Recrutamento') {
           const usuariosRec = usuarioRequest.data;
            setUsuarios(usuariosRec);
        } else if (usuario.authorities[0] === 'PLAN_N1') {
           const usuariosPlan = usuarioRequest.data.filter(u=>u.permissoes =='PLAN_N1'||u.permissoes =='PLAN_N1').map((u,i)=>({...u,NO:i+1,nome:u.nome +' '+u.apelido,permissoes:u.permissoes==='PLAN_N1'?'Chefe da Planificao':'Tcnico de Planificao',ativo:u.ativo ='true'?'Activo':'Inativo'}));
           setUsuarios(usuariosPlan);

        } else {
            const usuariosAll = usuarioRequest.data.map((c, i) => ({
                ...c, NO: i + 1, ativo: c.ativo = true ? 'Activo' : 'Inativo', permissoes: c.permissoes === 'ADMIN' ? 'Administrador' : c.permissoes === 'DPT' ? 'Chefe do Departamento' :
                    c.permissoes === 'RA_N1' ? 'Chefe do Registo Academico' : c.permissoes === 'PLAN_N1' ? 'Chefe da Planificao' :
                        c.permissoes === 'RA_N2' || c.permissoes === 'RA_N3' ? 'Tcnico do Registo Acadmico ' : c.permissoes === 'PLANIFICACAO' ? 'Tcnico de Planificao' : 'Estudante'
            }));
            setUsuarios(usuariosAll);
            // usuarios = usuarioRequest.data;
            departamentos = dptRequest.data

        }

        setUpdateDom(!updateDom);
        setCarregando(false);
    }
    async function usuarioLagado() {
        // setCarregando(true);
        // const usuarioDpt = await requesicoes().getUsuarioLogado(usuario.sub, '?campos=id,numero')
        // dpt = usuarioDpt.data
        // setCarregando(false)
        // setUpdateDom(!updateDom);
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {

        setOpen(false);
    };

    const options = {
        // filterType: "checkbox",
        responsive: "standard",
        elevation: 10,
        print: true,
        selectToolbarPlacement: "true",
        selectableRowsOnClick: true,
        expandableRows: false,
        expandableRowsHeader: false,
        isRowExpandable: (dataIndex, expandedRows) => {
            const { errors } = usuarios[dataIndex];
            return errors !== null && errors !== undefined;
        },
        onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
            console.log(curExpanded, allExpanded, rowsExpanded),
        renderExpandableRow: (rowData, rowMeta) => {

            return (
                <div
                    style={{
                        marginLeft: "90%",
                        width: "100%",
                        marginTop: "3%",
                        marginBottom: "3%"
                    }}
                >
                    <MUIDataTable
                        columns={errorCols}
                        tableData={usuarios[5]}
                        options={errorOptions}
                    />
                </div>
            );
        },
        customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
            console.log(selectedRows, displayData);
            return <div> am youre toolbar header </div>;
        },
        nextPath(path) {
            this.props.history.push(path);
        },
        customToolbar: () => {

            return (
                <Fab onClick={handleClickOpen} variant="extended" size="medium"
                    style={{
                        position: 'fixed',
                        bottom: '60px', zIndex: 500,
                        right: '15px'
                    }} color="primary">Adicionar</Fab>
            );
        },
        onRowSelectionChange: (
            currentRowsSelected,
            allRowsSelected,
            rowsSelected
        ) => {
            if (
                allRowsSelected !== null &&
                allRowsSelected !== undefined &&
                allRowsSelected.length > 0
            ) {
                setIsFormValid(true);
            } else {
                setIsFormValid(false);
            }
            console.log(currentRowsSelected, allRowsSelected, rowsSelected);
            rowsSelected.map((item) => console.log(usuarios[item]));
        }
    };

    const validationSchema = Yup.object().shape({
        nome: Yup.string().min(3, "Deve ter no mnimo 3 caracteres").required("Campo Obrigatrio"),
        apelido: Yup.string().min(3, "Deve ter no mnimo 3 caracteres").required("Campo Obrigatrio"),
        telemovel: Yup.string().min(9, "Deve ter no mnimo 9 dgitos").required("Campo Obrigatrio"),
        sexo: Yup.string().oneOf(["Masculino", "Femenino"], "Required").required("Campo Obrigatorio"),
        login: Yup.string().min(3, "Deve ter no mnimo 3 caracteres").required("Campo Obrigatrio. Deve introduzir nome de usuario ou email"),
        password: Yup.string().min(3, "Deve ter no mnimo 3 caracteres").required("Campo Obrigatrio"),
        passwordRept: Yup.string()
            .min(3, "Deve ter no mnimo 3 caracteres")
            .required("Campo Obrigatrio")
            .oneOf([Yup.ref('password'), null], 'As senhas devem coincidir'), // Esta linha adiciona a validao

    })

    return (
        <div>
            {carregando ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                    <MoonLoader color="#36d7b7" />
                </div>

            ) :
                usuarios.length !== 0 &&
                <MuiThemeProvider theme={getMuiTheme()}>
                    <MUIDataTable
                        title={'Lista de Usuarios'}
                        columns={columns(usuarios)}
                        data={usuarios}
                        options={options}
                    />
                </MuiThemeProvider>

            }

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Cadastrar Novo Usurio</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Preencha os dados do Usurio que pretende cadastrar
                    </DialogContentText>
                    <Formik initialValues={{}} validationSchema={validationSchema}

                        onSubmit={(values, { setSubmitting }) => {
                            async function save(values) {
                                // console.log(' Values ', JSON.stringify(values));
                                try {

                                    await Promise.resolve(requesicoes().postUsuarios([values]));
                                    Swal.fire(
                                        'Sucess!',
                                        'Curso cadastrado com sucesso!',
                                        'success'
                                    )
                                    init();

                                } catch (error) {
                                    console.log(error.response);
                                    if (error.response.data.status < 500) {

                                        Swal.fire({
                                            icon: 'warning',
                                            title: 'Oops...',
                                            text: error.response.data.message === 'No message available' ? 'Tenta novamente mais tarde a operao ' : error.response.data.message,
                                            footer: 'Se o erro pressistir porfavor contacte o Centro Informatico'
                                        })
                                    } else {
                                        Swal.fire({
                                            icon: 'error',
                                            title: 'Oops...',
                                            text: 'Ocorreu um erro ao cadastrar Usuario!!',
                                            footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                                        })
                                    }
                                }
                            }
                            save(values);
                            setTimeout(() => {
                                setSubmitting(false);
                            }, 1000);
                        }}>
                        {(props) => {
                            const { isSubmitting, setFieldValue, values } = props;
                            return (
                                <Form>

                                    <Grid container spacing={2}>
                                        <Grid item xs={4} sm={3}>
                                            <Input name="apelido"
                                                label="Apelido"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='apelido' />}
                                                error={props.errors.apelido && props.touched.apelido} />
                                        </Grid>
                                        <Grid item xs={8} sm={5}>
                                            <Input name="nome"
                                                label="Nome Completo (Sem incluir Apelido)"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='nome' />}
                                                error={props.errors.nome && props.touched.nome} />

                                        </Grid>
                                        <Grid item xs={12} sm={4} className="AppInput">

                                            <FormControl component="fieldset" >
                                                <Typography variant="body1" gutterBottom>Sexo</Typography>
                                                <RadioGroup row aria-label="sexo" name="sexo" value={values.sexo}
                                                    onChange={(e) => { setFieldValue('sexo', e.target.value); }}>
                                                    {sexos.map((opt, i) =>
                                                        <FormControlLabel key={i} value={opt} control={<Radio />} label={opt === 'Masculino' ? 'M' : 'F'} />)}
                                                </RadioGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </Grid>
                                        <Grid item xs={12} sm={4}>
                                            <Input name="login"
                                                label="User/Email"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='login' />}
                                                error={props.errors.login && props.touched.login} />
                                        </Grid>
                                        <Grid item xs={12} sm={4}>
                                            <Input name="password" type="password"
                                                label="Senha"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='password' />}
                                                error={props.errors.password && props.touched.password} />
                                        </Grid>
                                        <Grid item xs={12} sm={4}>
                                            <Input name="passwordRept" type="password"
                                                label="Repetir Senha"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='passwordRept' />}
                                                error={props.errors.passwordRept && props.touched.passwordRept} />
                                        </Grid>
                                        <Grid item xs={12} sm={4}>
                                            <Input name="telemovel"
                                                label="Contacto"
                                                placeholder="Digite Aqui..."
                                                helperText={<ErrorMessage name='telemovel' />}
                                                error={props.errors.telemovel && props.touched.telemovel} />
                                        </Grid>
                                        <Grid item xs={12} sm={8} >
                                            <Autocomplete onChange={(e, v) => {
                                                setFieldValue('permissoes', v.values);
                                                if (v.values === 'DPT') {
                                                    setIsCampoHabilitado(true);
                                                } else {
                                                    setIsCampoHabilitado(false);
                                                }
                                            }}
                                                fullWidth name="permissoes"
                                                getOptionLabel={option => option.label}
                                                options={permissoesUsuario.filter(item => item.values !== 'ESTUDANTE' && item.values !== 'PROFESSOR')
                                                } style={{ minWidth: 200 }}
                                                renderInput={(params) => <TextField variant="outlined" margin="dense" label="Privilgio"
                                                    fullWidth {...params} placeholder="Seleccione uma opo" />}
                                            />

                                        </Grid>
                                        {isCampoHabilitado && (
                                            <Grid item xs={12} sm={8} >
                                                <Autocomplete onChange={(e, v) => {
                                                    setFieldValue('dpt', v);
                                                }}
                                                    fullWidth name="dpt"
                                                    getOptionLabel={option => option.nome}
                                                    options={departamentos} style={{ minWidth: 200 }}
                                                    renderInput={(params) => <TextField variant="outlined" margin="dense" label="Departamento"
                                                        fullWidth {...params} placeholder="Seleccione uma opo" />}
                                                />

                                            </Grid>
                                        )}

                                    </Grid>
                                    <Button onClick={handleClose}>Cancelar</Button>
                                    <Button type='submit' onClick={handleClose}>Salvar</Button>

                                </Form>)
                        }}
                    </Formik>
                </DialogContent>
                <DialogActions>
                </DialogActions>
            </Dialog>
        </div>

    );
}
